import cTitle from 'components/title';	
import { MessageBox } from 'mint-ui';
  export default {
    data() {
      return {
 		value:3 ,
         comment:''   
       };
    },
    methods: {
        reComment()
        {
            if(this.comment.length==0)
            {
            MessageBox({
                title: '提示',
                message: '您还没有输入相关的评论内容',
                showCancelButton: true
                }).then(action => {
                if(action =='confirm')
                {

                    if(this.$route.params.from==1 )
                    {
                        //form 0-表示我的评价 1-订单列表的评价
                        this.$router.go('member');

                    }else
                    {
                        //this.$router.go({name:'myEvaluation',params:{selected:1}});
                        //this.$router.replace(path)('/myEvaluation');
                        this.$router.push({name:'myEvaluation',params:{selected:1}});

                    }
                

                }
    
                });
            }
        }

    },
    components: { cTitle}
  };


